  import { mapActions } from 'vuex';
  import SearchBar from '@/components/elements/SearchBar';

  export default {
    name: 'Navbar',
    components: {
      SearchBar,
    },
    data() {
      return {
        user: []
      };
    },
    async created() {
      await this.getUid().then((result) => this.user.push(result));
    },
    methods: {
      ...mapActions({
        getUid: 'auth/getUid',
      }),
      signOut() {
        try {
					this.$store.dispatch('auth/signOut');
        } catch (error) {
          throw error.message;
        }
      }
    }
  };

