import { mapActions } from 'vuex';

export default {
  name: 'DropDown',
  props: ['searchedData', 'loaderData', 'type'],
  computed: {
    activeEl: {
      set() {},
      get() {
        return this.type === 'anime' ? 'anime' : 'manga';
      }
    }
  },
  methods: {
    ...mapActions({
      computeRoute: 'computeRoute',
    }),
    changeType(el) {
      this.activeEl = el;
      this.$emit('changeType', el);
    }
  }
};

