import Messages from '@/utils/messages';

export default {
  name: 'Authentication',
  computed: {
    error() {
      return this.$store.getters.error;
    }
  },
  watch: {
    error(fireBaseError) {
      this.$error(Messages[fireBaseError.code]);
    }
  },
  mounted() {
    M.AutoInit();
  }
};
